/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
}

@font-face {
  font-family: "Norwester", sans-serif;
  src: url("../../fonts/norwester.otf");
}
@font-face {
  font-family: "Montserrat-SemiBold", sans-serif;
  src: url("../../fonts/Montserrat-SemiBold.ttf");
}
@font-face {
  font-family: "Montserrat-Regular", sans-serif;
  src: url("../../fonts/Montserrat-Regular.ttf");
}
.yellow-part {
  color: var(--secondary);
}

h1 {
  font-size: 38px;
  font-family: Norwester, sans-serif;
  font-weight: 500;
  color: var(--accent);
  margin: 0px 0px 25px 0px;
}
@media (min-width: 1024px) {
  h1 {
    font-size: 48px;
  }
}

h2 {
  font-size: 20px;
  font-family: Montserrat-SemiBold, sans-serif;
  color: var(--accent);
  margin: 0px 0px 16px 0px;
}
@media (min-width: 1024px) {
  h2 {
    font-size: 24px;
  }
}

p {
  font-size: 16px;
  font-family: Montserrat-Regular, sans-serif;
  color: var(--accent);
  margin: 0px 0px 9px 0px;
}
@media (min-width: 1024px) {
  p {
    font-size: 18px;
  }
}

:root {
  --primary: #2b2a28;
  --secondary: #feed01;
  --accent: #fff7e6;
}

.services-title {
  text-align: center;
}
.services .underline:after {
  content: "";
  display: block;
  height: 1px;
  background: #fff7e6;
  width: 200px;
  margin: 4px auto 0;
}
@media (min-width: 1024px) {
  .services .underline:after {
    width: 300px;
  }
}
.services .services-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 1150px;
  margin: 0 auto;
}
.services .services-wrapper .service {
  text-align: center;
  max-width: 350px;
  margin: 0 auto 30px;
}
.services .services-wrapper .service-icon-wrapper {
  border: 2px solid var(--secondary);
  border-radius: 100px;
  padding: 25px;
  display: inline-block;
  margin-bottom: 10px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-icon-wrapper {
    padding: 35px;
  }
}
.services .services-wrapper .service-title {
  font-family: Norwester;
  font-size: 28px;
}
@media (min-width: 1024px) {
  .services .services-wrapper .service-title {
    font-size: 36px;
  }
}
.services .services-wrapper .service-about {
  width: 80%;
  margin: 0 auto;
}
@media (min-width: 667px) {
  .services .services-wrapper .service {
    max-width: 33.3333333333%;
  }
}
@media (min-width: 667px) {
  .services .services-wrapper {
    flex-direction: row;
  }
}

body {
  background: var(--primary);
}